import {wfdList1} from "../utils/wfd-list";
import {getSpeech} from "../utils/utils";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";

const WfdStudy = () => {
  const [currentSentence, setCurrentSentence] = useState("");
  const [currentIdx, setCurrentIdx] = useState(0);
  const [answer, setAnswer] = useState("");
  const [rightAnswer, setRightAnswer] = useState(0);
  const [showAnswer, setShowAnswer] = useState(0);
  const [showFront, setShowFront] = useState(false);

  //음성 변환 목소리 preload
  useEffect(() => {
    window.speechSynthesis.getVoices();
    setCurrentSentence(wfdList1[currentIdx]);
  }, []);

  const playButton = () => {
    console.log(currentSentence);
    getSpeech(currentSentence);
  };

  const answerInput = (e) => {
    const { value } = e.target;
    setAnswer(value);
  };

  const moveQuestion = (target) => {
    let nextTarget = currentIdx
    if (target === 'next') {
      nextTarget += 1;
    } else if (target === 'prev') {
      nextTarget -= 1;
    } else {
      if (isNaN(target)) {
        alert("숫자만 입력해주세요.");
        return;
      }
      if (target === '') {
        alert("숫자를 입력해주세요.");
        return;
      }
      nextTarget = parseInt(target) - 1;
    }

    console.log(nextTarget);

    if (nextTarget >= wfdList1.length) {
      alert("마지막 문제입니다.");
      nextTarget = 0;
    }
    if (nextTarget < 0) {
      alert("처음 문제입니다.");
      nextTarget = 0;
    }
    setShowAnswer(0);
    setCurrentIdx(nextTarget);
    setCurrentSentence(wfdList1[nextTarget]);
  }

  const answerCheck = () => {
    if (showAnswer === 0) {
      setShowAnswer(1);
    } else {
      setShowAnswer(0);
    }

    if (answer === currentSentence) {
      setRightAnswer(1);
    } else {
      setRightAnswer(0);
    }
  }

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD(영어)로 이동</Link></li>
        <li><Link to={'/wfdv2'}>WFDv2(영어)로 이동</Link></li>
        <li><Link to={'/we'}>WE(영어)로 이동</Link></li>
        <li><Link to={'/wev2'}>WEv2(영어)로 이동</Link></li>
        <li><Link to={'/sst'}>SST(영어)로 이동</Link></li>
        <li><Link to={'/rs'}>RS(영어)로 이동</Link></li>
        <li><Link to={'/asq'}>ASQ(영어)로 이동</Link></li>
      </ul>
      <h1>WFD(영어)</h1>
      <div className="box">
        <button onClick={playButton}>음성 재생</button>
        <div>
          앞부분 보기<input type={"checkbox"} onChange={(event) => setShowFront(event.target.checked)} />
          <span style={{display: showFront? 'block' : 'none'}}>
            {`${currentSentence.split(' ')[0]} ${currentSentence.split(' ')[1]}`}
          </span>
        </div>
        <div>현재 문제 <span style={{color: 'red', fontWeight: 'bold', fontSize: '15px'}}>{parseInt(currentIdx)+1}</span>번</div>
        <div>
          <input onChange={answerInput} value={answer} autoComplete="off" style={{width: '600px'}} />
        </div>
        <br />
        <button onClick={answerCheck}>답 확인</button>
        {/*<div style={{display: showAnswer === 0 ? 'none' : 'block'}}><span>정답: {currentSentence}</span></div>
        <div style={{display: showAnswer === 0 ? 'none' : 'block'}}><span>내가 적은거: {answer}</span></div>*/}
        <div style={{display: showAnswer === 0 ? 'none' : 'block', textAlign: 'left'}}>
          <div style={{width: '800px', margin: '0 auto'}} >
            <span>정답: {currentSentence}</span><br />
            <span>내가: {answer}</span>
          </div>
        </div>
        <br />
        <div><span>{rightAnswer === 0 ? '오답' : '정답'}</span></div>
        <br />
        <button onClick={() => moveQuestion('prev')}>이전 문제</button>
        <button onClick={() => moveQuestion('next')}>다음 문제</button>
        <br/>
        <label htmlFor={'jump'}>문제 바로이동: <input onChange={(event) => moveQuestion(event.target.value)} name={'jump'} /></label>
      </div>
    </div>
  );
};

export default WfdStudy;