import {wfdList1} from "../utils/wfd-list";
import {getSpeech} from "../utils/utils";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";

const WfdStudyV2 = () => {
  const [answer, setAnswer] = useState([]);
  const [rightAnswer, setRightAnswer] = useState([]);
  const [showAnswer, setShowAnswer] = useState([]);
  const [studyMode, setStudyMode] = useState(false); // wfd, we
  const [voiceMode, setVoiceMode] = useState(true); // wfd, we
  // const [showFront, setShowFront] = useState(false);

  //음성 변환 목소리 preload
  useEffect(() => {
    const defaultValue = [];
    const defaultRightAnswer = [];
    for (let i = 0; i < wfdList1.length; i++) {
      defaultValue.push('');
      defaultRightAnswer.push(false)
    }
    setAnswer(defaultValue);
    setRightAnswer(defaultRightAnswer);
    setShowAnswer(defaultRightAnswer);
    window.speechSynthesis.getVoices();
  }, []);

  const playButton = (targetIdx) => {
    const currentSentence = wfdList1[targetIdx];
    getSpeech(currentSentence);
  };

  const answerInput = (value, idx) => {
    const newAnswer = [...answer];
    newAnswer[idx] = value;
    setAnswer(newAnswer);
  };

  const enterDown = (e, idx) => {
    if (e.key === 'Enter') {
      // console.log('ENTER?????');
      answerCheck(idx);
    }
  }

  const answerCheck = (idx) => {
    const newShowAnswer = [...showAnswer];
    const newRightAnswer = [...rightAnswer];
    if (showAnswer[idx] === false && voiceMode === true) {
      playButton(idx);
    }
    newShowAnswer[idx] = !showAnswer[idx];
    setShowAnswer(newShowAnswer);

    if (answer[idx] === wfdList1[idx]) {
      newRightAnswer[idx] = true;
    } else {
      newRightAnswer[idx] = false;
    }
    setRightAnswer(newRightAnswer);
  }

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD(영어)로 이동</Link></li>
        <li><Link to={'/wfdv2'}>WFDv2(영어)로 이동</Link></li>
        <li><Link to={'/we'}>WE(영어)로 이동</Link></li>
        <li><Link to={'/wev2'}>WEv2(영어)로 이동</Link></li>
        <li><Link to={'/sst'}>SST(영어)로 이동</Link></li>
        <li><Link to={'/rs'}>RS(영어)로 이동</Link></li>
        <li><Link to={'/asq'}>ASQ(영어)로 이동</Link></li>
      </ul>
      <h1>WFD v2(영어)</h1>
      <div className="box">
        <div>
          다보기 모드<input type={"checkbox"} onChange={(event) => setStudyMode(event.target.checked)} checked={studyMode} /><br />
          음성 자동재생<input type={"checkbox"} onChange={(event) => setVoiceMode(event.target.checked)} checked={voiceMode} />
        </div>
        <br />
        <table style={{border: '1px solid black', borderCollapse: 'collapse', marginLeft: 'auto', marginRight: 'auto'}}>
          <colgroup>
            <col width="20px" />
            <col width="50px" />
            <col width="650px" />
            <col width="70px" />
          </colgroup>
          <thead>
          <tr>
            <th>번호</th>
            <th>정답</th>
            <th>문제</th>
            <th>재생</th>
          </tr>
          </thead>
          <tbody>
          {wfdList1.map((item, idx) => {
            return (
              <tr key={idx}>
                <td>{idx + 1}</td>
                <td><span>{rightAnswer[idx] === false ? '오답' : '정답'}</span></td>
                <td style={{textAlign: 'left'}}>
                  <div>
                    {studyMode ? item:`${item.split(' ')[0]} ${item.split(' ')[1]} ${item.split(' ')[2]}`}
                  </div>
                  <div>
                    <input type='text' onChange={(e) => answerInput(e.target.value, idx)} onKeyDown={(e) => enterDown(e, idx)} value={answer[idx]} autoComplete="off" style={{width: '600px', fontSize: '16px'}} />
                  </div>
                  <div style={{display: showAnswer[idx] === false ? 'none' : 'block', textAlign: 'left'}}>
                    <div style={{width: '600px'}} >
                      <span>정답: {item}</span><br />
                      <span>내가: {answer[idx]}</span>
                    </div>
                  </div>
                </td>
                <td><button onClick={() => playButton(idx)}>재생</button></td>
              </tr>
            )
          })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default WfdStudyV2;