import {useState} from "react";
import {Link} from "react-router-dom";
import {AsqList1, AsqAnswer} from "../utils/asq-list";
import {getSpeech} from "../utils/utils";

const AsqStudy = () => {
  // 61 번까지 매우 빈도 높은

  const playButton = (targetIdx) => {
    const currentSentence = AsqList1[targetIdx];
    getSpeech(currentSentence);
  };

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD(영어)로 이동</Link></li>
        <li><Link to={'/wfdv2'}>WFDv2(영어)로 이동</Link></li>
        <li><Link to={'/we'}>WE(영어)로 이동</Link></li>
        <li><Link to={'/wev2'}>WEv2(영어)로 이동</Link></li>
        <li><Link to={'/sst'}>SST(영어)로 이동</Link></li>
        <li><Link to={'/rs'}>RS(영어)로 이동</Link></li>
        <li><Link to={'/asq'}>ASQ(영어)로 이동</Link></li>
      </ul>
      <h1>ASQ(영어)</h1>
      <h2>61 번까지 매우 빈도 높은 빈도</h2>
      <div className="box">
        <br />
        <table style={{border: '1px solid black', borderCollapse: 'collapse', marginLeft: 'auto', marginRight: 'auto'}}>
          <colgroup>
            <col width="20px" />
            <col width="50px" />
            <col width="650px" />
            <col width="70px" />
          </colgroup>
          <thead>
          <tr>
            <th>번호</th>
            <th>정답</th>
            <th>문제</th>
            <th>재생</th>
          </tr>
          </thead>
          <tbody>
          {AsqList1.map((item, idx) => {
            return (
              <tr key={idx}>
                <td>{idx + 1}</td>
                <td><span>{AsqAnswer[idx]}</span></td>
                <td style={{textAlign: 'left'}}>
                  <div>
                    <span style={{fontSize: '16px'}}>{item}</span>
                  </div>
                </td>
                <td><button onClick={() => playButton(idx)}>재생</button></td>
              </tr>
            )
          })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default AsqStudy;