import {useState} from "react";
import {Link} from "react-router-dom";
import {SstList} from "../utils/sst-list";

const SstStudy = () => {
  const [answer1, setAnswer1] = useState("");
  const [answer2, setAnswer2] = useState("");
  const [answer3, setAnswer3] = useState("");

  const [rightAnswer1, setRightAnswer1] = useState(0);
  const [rightAnswer2, setRightAnswer2] = useState(0);
  const [rightAnswer3, setRightAnswer3] = useState(0);

  const [showAnswer1, setShowAnswer1] = useState(0);
  const [showAnswer2, setShowAnswer2] = useState(0);
  const [showAnswer3, setShowAnswer3] = useState(0);

  const [showAll, setShowAll] = useState('none');

  const answerInputSection1 = (e) => {
    const { value } = e.target;
    setAnswer1(value);
  };

  const answerInputSection2 = (e) => {
    const { value } = e.target;
    setAnswer2(value);
  };

  const answerInputSection3 = (e) => {
    const { value } = e.target;
    setAnswer3(value);
  };

  const answerCheck = (num) => {
    if (num === 1) {
      if (showAnswer1 === 0) {
        setShowAnswer1(1);
      } else {
        setShowAnswer1(0);
      }

      if (answer1 === SstList[0]) {
        setRightAnswer1(1);
      } else {
        setRightAnswer1(0);
      }
    }
    if (num === 2) {
      if (showAnswer2 === 0) {
        setShowAnswer2(1);
      } else {
        setShowAnswer2(0);
      }

      if (answer2 === SstList[1]) {
        setRightAnswer2(1);
      } else {
        setRightAnswer2(0);
      }
    }
    if (num === 3) {
      if (showAnswer3 === 0) {
        setShowAnswer3(1);
      } else {
        setShowAnswer3(0);
      }

      if (answer3 === SstList[2]) {
        setRightAnswer3(1);
      } else {
        setRightAnswer3(0);
      }
    }
  }

  const changeShowAll = () => {
    if (showAll === 'none') {
      setShowAll('block');
    } else {
      setShowAll('none');
    }
  };

  return (
    <div className="App">
      <ul style={{listStyle: 'none'}}>
        <li><Link to={'/'}>WFD(영어)로 이동</Link></li>
        <li><Link to={'/wfdv2'}>WFDv2(영어)로 이동</Link></li>
        <li><Link to={'/we'}>WE(영어)로 이동</Link></li>
        <li><Link to={'/wev2'}>WEv2(영어)로 이동</Link></li>
        <li><Link to={'/sst'}>SST(영어)로 이동</Link></li>
        <li><Link to={'/rs'}>RS(영어)로 이동</Link></li>
        <li><Link to={'/asq'}>ASQ(영어)로 이동</Link></li>
      </ul>
      <h1>WE(영어)</h1>
      <div className="box">
        <button onClick={changeShowAll}>전체 템플릿 보기</button>
        <div>
          <ul style={{listStyle: 'none'}}>
            <li>주제: !!!!</li>
            <li>연관된 단어 나열: 00(the xx, xx, xx, xx...)</li>
            <li>??: 문장</li>
            <li>00 and 00: 상반된 두 단어</li>
          </ul>
        </div>
        <div>
          <textarea onChange={answerInputSection1} autoComplete="off" rows="8" style={{width: '740px', fontSize: '14px'}} value={answer1}></textarea>
          <div><span>{rightAnswer1 === 0 ? '오답' : '정답'}</span></div>
          <div style={{display: showAnswer1 === 0 ? 'none' : 'block', textAlign: 'left'}}>
            <div style={{width: '800px', margin: '0 auto'}} >
              <span>정답: {SstList[0]}</span>
            </div>
          </div>
          <button onClick={() => answerCheck(1)}>답 확인</button>
        </div>
        <div>
          <textarea onChange={answerInputSection2} autoComplete="off" rows="8" style={{width: '740px', fontSize: '14px'}} value={answer2}></textarea>
          <div><span>{rightAnswer2 === 0 ? '오답' : '정답'}</span></div>
          <div style={{display: showAnswer2 === 0 ? 'none' : 'block', textAlign: 'left'}}>
            <div style={{width: '800px', margin: '0 auto'}} >
              <span>정답: {SstList[1]}</span>
            </div>
          </div>
          <button onClick={() => answerCheck(2)}>답 확인</button>
        </div>
        <div>
          <textarea onChange={answerInputSection3} autoComplete="off" rows="8" style={{width: '740px', fontSize: '14px'}} value={answer3}></textarea>
          <div><span>{rightAnswer3 === 0 ? '오답' : '정답'}</span></div>
          <div style={{display: showAnswer3 === 0 ? 'none' : 'block', textAlign: 'left'}}>
            <div style={{width: '800px', margin: '0 auto'}} >
              <span>정답: {SstList[2]}</span>
            </div>
          </div>
          <button onClick={() => answerCheck(3)}>답 확인</button>
        </div>
      </div>
      <div className={"box"} style={{fontSize: '20px', display: showAll}}>
        <div style={{width: '800px', height: '500px', margin: '0 auto'}}>
          <p style={{textAlign: 'left'}}>
            !!!! has been significantly focused as it included that sentence linked to 00.
          </p>
          <p style={{textAlign: 'left'}}>
            Notably, it also denotes that ??.
          </p>
          <p style={{textAlign: 'left'}}>
            So, considering the most substantial insight, it can be stated that the overall correlation between 00 and 00 should not be ignored under any circumstances.
          </p>
        </div>
      </div>
    </div>
  );
};

export default SstStudy