const getSpeech = (text) => {
  let voices = [];

  //디바이스에 내장된 voice를 가져온다.
  const setVoiceList = () => {
    voices = window.speechSynthesis.getVoices();
  };

  setVoiceList();

  if (window.speechSynthesis.onvoiceschanged !== undefined) {
    //voice list에 변경됐을때, voice를 다시 가져온다.
    window.speechSynthesis.onvoiceschanged = setVoiceList;
  }

  const speech = (txt) => {
    const lang = "en-UK";
    const utterThis = new SpeechSynthesisUtterance(txt);
    utterThis.lang = lang;

    /* 한국어 vocie 찾기
      디바이스 별로 한국어는 ko-KR 또는 ko_KR로 voice가 정의되어 있다.
    */
    const en_voice = voices.find((elem) => {
      return elem.name === 'Google UK English Female'
    });
    //힌국어 voice가 있다면 ? utterance에 목소리를 설정한다 : 리턴하여 목소리가 나오지 않도록 한다.
    if (en_voice) {
      utterThis.volume = 0.5;
      utterThis.voice = en_voice;
    } else {
      return;
    }

    //utterance를 재생(speak)한다.
    console.log(utterThis);
    window.speechSynthesis.speak(utterThis);
  };

  speech(text);
};

export {
  getSpeech
}